html * {
	box-sizing: border-box;
}

#wrapper {
	position: relative;
	overflow: hidden;
}

body {
	min-width: 360px;
	font-family: 'Arial';
	font-weight: 400;
	font-size: 14px;
	color: #333333;
}

a {
	text-decoration: none;
	transition: all 0.3s ease;
	color: #333333;

	&:hover {
		text-decoration: none;
		transition: all 0.3s ease;
	}
}

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	margin: 0;
	font-weight: 700;
	font-size: 16px;
}

img {
	max-width: 100%;
}

p {
	margin: 0 0 10px;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.container {
	max-width: 489px;
	margin: 0 auto;
	padding: 0 20px;
}

.profile-header {
	padding: 23px 0 12px;
	position: relative;

	.profile-avatar {
		width: 120px;
		height: 120px;
		overflow: hidden;
		border-radius: 50%;
		position: absolute;
		top: 16px;
		left: 0;
	}

	.profile-user {
		padding-left: 130px;
		margin-bottom: 7px;

		h1 {
			margin-bottom: 3px;
		}

		.user-position {
			font-size: 12px;
			color: #808080
		}
	}

	.profile-user-info {
		padding-left: 63px;

		.text-holder {
			border: 1px solid #cccccc;
			border-radius: 5px;
			background-color: #fffbc8;
			padding: 8px 7px 4px 67px;
			font-size: 12px;
		}
	}
}

.profile-services {
	padding: 0 0 23px 0;

	.services-header {
		display: flex;
		justify-content: flex-end;
		font-size: 13px;
		padding: 0 33px 7px 0;
	}

	.service-holder {
		border-bottom: 1px solid #cccccc;
		border-top: 1px solid #cccccc;
		padding: 15px 0 13px;
	}

	.services-main {
		display: flex;
		justify-content: space-between;
		border-left: 1px solid #7eb4ca;
		padding: 6px 40px 5px 0;
		font-size: 13px;

		&:first-child {
			border-left: 1px solid #83b26d;

			.progress-bar {
				background-color: #b1e19b;
			}
		}

		.title {
			position: relative;
			width: calc(100% - 30px);
			padding-left: 5px;
		}

		.progress-bar {
			position: absolute;
			top: -6px;
			bottom: -3px;
			left: 0;
			right: 12%;
			background-color: #ace2f8;
			z-index: -1;
			border-radius: 0 3px 3px 0;
		}

		.counter {
			width: 30px;
			text-align: right
		}
	}

	.services-footer {
		display: flex;
		justify-content: space-between;
		padding-right: 42px;
		font-size: 16px;
		font-weight: 700;
		padding-top: 14px;
	}
}

.profile-reviews {
	padding-bottom: 5px;

	.reviews-info {
		display: flex;
		justify-content: space-between;
		padding-bottom: 10px;

		.reviews-tab {
			display: flex;
			flex-wrap: wrap;
			align-items: baseline;
			margin: 0 -3px;

			.active {

				a {
					font-size: 14px;
					color: #333333;
					text-decoration: none;
					pointer-events: none;
    				cursor: default;
    				font-weight: 700;
				}
			}

			li {
				padding: 0 3px;

				a {
					font-size: 14px;
					color: #005da1;
					text-decoration: underline;
					transition: all 0.3s ease;

					&:hover {
						transition: all 0.3s ease;
						color: #fdd639;
					}
				}
			}
		}

		.reviews-tools {
			display: flex;
			margin: 0 -4px;

			li {
				padding: 0 4px;
			}

			a {
				padding-left: 20px;
				position: relative;

				&:hover {
					
					i {
						transition: all 0.3s ease;
						color: #fdd639;
					}
				}
			}

			i {
				color: #73b4d5;
				position: absolute;
				top: 0;
				left: 2px;
				transition: all 0.3s ease;
			}
		}
	}

	.reviews-wrapper {
		.comment-info {
			padding-bottom: 12px;

			time {
				font-size: 11px;
				color: #808080;
			}

			.profile-nick {
				font-weight: 700;
			}
		}

		.comment-content {
			position: relative;
			background-color: #f2fbff;
			border: 1px solid #cfcfcf;
			padding: 15px 20px 3px;
			margin-bottom: 18px;
			line-height: 1.4;

			&:before,
			&:after {
				content: '';
				position: absolute;
				width: 0;
				height: 0;
				bottom: 100%;
			}

			&:before {
				left: 5%;
				border: 5px solid;
				border-color: transparent transparent #cfcfcf #cfcfcf;
			}

			&:after {
				left: 5.25%;
				border: 4px solid;
				border-color: transparent transparent #f2fbff #f2fbff;
			}

			p {
				margin-bottom: 7px;
			}
		}
	}
}

.comment-input-holder {
	background-color: #f2f2f2;
	padding: 29px 0 34px;

}

.footer-form {
	text-align: center;

	.feedback-input {
		width: 100%;
		border-style: none;
		min-height: 65px;
		vertical-align: text-top;
		overflow: hidden;
		border: 1px solid #a9a9a9;
		border-radius: 3px;
		margin-bottom: 23px;
		padding: 10px;
		resize: vertical;
	}
}

.button-submit {
	background-color: #fdd639;
	border-style: none;
	cursor: pointer;
	color: #333333;
	transition: all 0.3s ease;
	min-width: 282px;
	border-radius: 2em;
	padding: 11px;
	font-size: 16px;
	font-weight: 700;
	font-family: 'PT Sans';

	&:hover {
		transition: all 0.3s ease;
		color: #fdd639;
		background-color: #333333;
	}
}


@media (min-width: 480px) {
	.profile-header {
		.profile-user-info {
			.text-holder {
				font-size: 14px;
			}
		}
	}

	.profile-services {
		padding: 0 0 28px 130px;
	}

	.profile-reviews {
		.reviews-info {
			.reviews-tab {
				margin: 0 - 5px;

				li {
					padding: 0 5px;
				}
				.active {
					a {
						font-size: 16px;
					}
				}
			}

			.reviews-tools {
				margin: 0 -8px;

				li {
					padding: 0 8px;
				}
			}
		}
	}
}


